.App {
  position: relative;
  width: 100vw;
  height: 100vh;
}

button {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 200px;
  height: 100px;
  font-size: 50px;
  background-color: rgb(130, 72, 184);
  cursor: pointer;
}
